export default "/* ****************************** NAVIGATION BAR ****************************** */\r\n/* Both top and bottom parts of navbar */\r\n.navigation {position:fixed;width:100%;z-index:99;border-bottom:solid 2px black}\r\n.navigation a {color:white;padding:15px;font-family:Helvetica,sans-serif;font-size:13pt}\r\n.navigation a:hover {border-radius:5px;transition:0.7s ease;color:#272727;background-color:rgb(177, 177, 177);text-decoration:none;}\r\n/* The dark thick top-border */\r\n#header {overflow:hidden;background-color:#333;opacity:1;border-bottom:1px solid black; top:0;margin-top:-12px}\r\n/* The actual navigation with links */\r\n#navbar {overflow:hidden;opacity:1;}\r\n.navbar-toggle {background-color: #272727;}\r\n#navbarCollapse {text-align:center}\r\n/* Github, linkedIn and contact pages */\r\n.social-links {margin-top:14px}\r\n.social-links a {padding:25px;}\r\n/* Adjust links on navbar based on screen size */\r\n@media screen and (max-width:767px) {\r\n  .separator-vertical {display:none}\r\n  .separator-horizontal {display:block}\r\n  .blog-link {display:none}\r\n  .navigation a {padding:8px}\r\n  .social-links a {padding:25px}\r\n}\r\n@media screen and (min-width:767px) {\r\n  .separator-vertical {display:block}\r\n  .separator-horizontal {display:none}\r\n}\r\n@media screen and (min-width:767px) and (max-width:1400px) {\r\n  .separator-vertical {display:none}\r\n  .hide-links {display:none}\r\n  .blog-link {display:block}\r\n}\r\n@media screen and (min-width:1401px) {\r\n  .hide-links {display:block}\r\n  .blog-link {display:none}\r\n}\r\n/* Adjust social links on navbar based on screen size */\r\n@media screen and (min-width:1001px) and (max-width:3000px) {\r\n  .social-links {margin-top:15px}\r\n}\r\n@media screen and (min-width:768px) and (max-width:1000px) {\r\n  .social-links {display:none}\r\n}\r\n@media screen and (max-width:767px) {\r\n  .social-links {margin-top:20px;margin-bottom:20px}\r\n} \r\n\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"